import React from "react";
import { styled, Button, Table, TableContainer, TableHead, TableBody, TableRow, TableCell, tableCellClasses, Paper } from "@mui/material";

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
      backgroundColor: '#6f92be',
      color: theme.palette.common.white,
      fontWeight: 'bold',
      fontSize: 16,
    },
    [`&.${tableCellClasses.body}`]: {
      fontSize: 14,
    },
  }));
  
  const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
    // hide last border
    '&:last-child td, &:last-child th': {
      border: 0,
    },
  }));

function createData(region, prod_line, station, nc_id, timestamp, desc) {
    return { region, prod_line, station, nc_id, timestamp, desc };
}

const demoData = [
    createData('', 'EG', '', 'GI-700-3', '2024/08/05 12:53:25', ''),
    createData('', 'EG', '', 'GI-700-3', '2024/08/05 12:52:14', ''),
    createData('', 'RG', '', 'GI-700-3', '2024/08/04 09:12:01', ''),
    createData('', 'RG', '', 'SG-500-1', '2024/08/01 15:36:36', ': HMI'),
    createData('', 'RG', '', 'SG-500-1', '2024/08/01 15:35:25', ': HMI'),
    createData('', 'RG', '', 'SG-500-1', '2024/08/01 13:58:09', ': HMI'),
    createData('', 'MG', '', 'GI-700-4', '2024/07/29 16:44:48', ''),
    createData('', 'MG', '', 'Fanuc M-800i', '2024/07/29 08:37:27', 'A1 '),
    createData('', 'EG', '', 'GI-700-3', '2024/07/20 14:14:55', ''),
    createData('', 'RG', '', 'GI-700-3', '2024/07/19 14:55:32', ''),
    createData('', 'MG', '', 'Fanuc M-800i', '2024/07/11 13:50:11', ''),
    createData('', 'RG', '', 'SG-500-1', '2024/07/08 12:53:25', ': HMI'),
    createData('', 'MG', '', 'Fanuc M-800i', '2024/07/02 19:10:17', ''),
    createData('', 'MG', '', 'GI-700-4', '2024/06/30 16:14:52', ''),
    createData('', 'MG', '', 'Fanuc M-800i', '2024/06/29 10:16:30', 'A1 '),
    createData('', 'EG', '', 'GI-700-3', '2024/06/21 17:29:22', ': HMI'),
    
];

export default function Table_Alarm() {
    return (
        <TableContainer component={Paper}>
            <Table sx={{ minWidth: 640 }} aria-lable='nc_status table'>
                <TableHead>
                    <TableRow>
                        <StyledTableCell align="center"></StyledTableCell>
                        <StyledTableCell align="center"></StyledTableCell>
                        <StyledTableCell align="center"></StyledTableCell>
                        <StyledTableCell align="center"></StyledTableCell>
                        <StyledTableCell align="center"></StyledTableCell>
                        <StyledTableCell align="center"></StyledTableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {demoData.map((row) => (
                        <StyledTableRow key={row.region}>
                            <StyledTableCell component={'th'} scope="row" align='center'>
                                {row.region}
                            </StyledTableCell>
                            <StyledTableCell align='center'>{row.prod_line}</StyledTableCell>
                            <StyledTableCell align='center'>{row.station}</StyledTableCell>
                            <StyledTableCell align='center'>{row.nc_id}</StyledTableCell>
                            <StyledTableCell align='center'>{row.timestamp}</StyledTableCell>
                            <StyledTableCell align='center'>{row.desc}</StyledTableCell>
                        </StyledTableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}